.movie-poster {
    position: relative;
    padding-top: 150%;
    border-radius: 3px;
    overflow: hidden;
    -webkit-box-shadow: 0 0 10px rgba(0,0,0,.5);
            box-shadow: 0 0 10px rgba(0,0,0,.5);
}

.movie-poster > img {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
}



